import React from 'react';
import classname from 'classnames';
import "@fortawesome/fontawesome-free/css/all.min.css";
import ReactLoading from 'react-loading';

export enum BadgeVariant {
    default = 'default',
    primary = 'primary',
    secondary = 'secondary',
    success = 'success',
    info = 'info',
    danger = 'danger',
    warning = 'warning',
}

export interface IProps {
    outlined?: boolean;
    label: string,
    variant?: BadgeVariant
}

export const Badge: React.FC<IProps> = ({ 
    label,
    outlined = false,
    variant = BadgeVariant.default,
    ...props
 }) => {

    return (
        <span className={classname({
            "text-xs font-semibold inline-block py-1 px-2 rounded uppercase last:mr-0 mr-1": true,
            // Outline none
            'bg-gray-300 outline-gray-600 ' : BadgeVariant.default == variant,
            'bg-teal-300 outline-teal-600' : BadgeVariant.primary == variant,
            'bg-orange-400 outline-orange-600 ' : BadgeVariant.secondary == variant,
            'bg-teal-400  outline-teal-600' : BadgeVariant.success == variant,
            'bg-cyan-300 outline-cyan-600' : BadgeVariant.info == variant,
            'bg-amber-400 outline-amber-600' : BadgeVariant.warning == variant,
            'bg-red-400 bg-red-600 ' : BadgeVariant.danger == variant,
            // Outline
            'outline outline-1 bg-transparent' : outlined,
            'text-gray-600 outline-gray-600' : BadgeVariant.default == variant && outlined,
            'text-teal-300 outline-teal-300' : BadgeVariant.primary == variant && outlined,
            'text-orange-400 outline-orange-400' : BadgeVariant.secondary == variant && outlined,
            'text-teal-400 outline-teal-400' : BadgeVariant.success == variant && outlined,
            'text-cyan-300 outline-cyan-300' : BadgeVariant.info == variant && outlined,
            'text-amber-400 outline-amber-400' : BadgeVariant.warning == variant && outlined,
            'text-red-400 outline-red-400' : BadgeVariant.danger == variant && outlined,
        })}>
            {label}
        </span>
    )
}